import React, {useState} from 'react'
import { Modal, Form, Row, Col, Input, DatePicker, Button, Select } from 'antd'
import { nationalityData, provinceData, districtData, communeData, villageData } from '../../../context/headerContext'
import { ListSelect } from '../../../static/own-comp'
import { convertToCommune, convertToDistrict, convertToVillage } from '../../../function/fn'
import Vaccine from './vaccine'

let {Option} = Select

export default function Tab2({open, setOpen}) {
    let [form] = Form.useForm()

    const [openVaccine, setOpenVaccine] = useState(false)

    const [province, setProvince] = useState("")
    const [district, setDistrict] = useState("")
    const [commune, setCommune] = useState("")

    console.log(nationalityData)

    const setToProviceFn = (e) => {
        form.setFieldsValue({
            province: e,
            district: null,
            commune: null,
            village: null,
        });

        setProvince(e)
        setDistrict("")
        setCommune("")
    };


    const setToDistrictFn = (e) => {
        form.setFieldsValue({
            district: e,
            commune: null,
            village: null,
        });

        setDistrict(e)
        setCommune("")
    };

    const setToCommuneFn = (e) => {
        form.setFieldsValue({
            commune: e,
            village: null,
        });

        setCommune(e)
    };

    const setToVillageFn = (e) => {
        form.setFieldsValue({
            village: e
        });
    };


    return (
        <Modal
            visible={open}
            onCancel={() => setOpen(false)}
            title="១.ព័ត៌មានផ្ទាល់ខ្លួនអ្នកជំងឺ"
            footer={null}
        >
            <Vaccine open={openVaccine} setOpen={setOpenVaccine} />
            <Form
                name="tab2"
                form={form}
            >
                <Row>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name=""
                        >
                            <Input placeholder="គោត្តនាម" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name=""
                        >
                            <Input placeholder="នាម" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name=""
                        >
                            <Select placeholder="ភេទ">
                                <Option value="ប្រុស" key="ប្រុស">ប្រុស</Option>
                                <Option value="ស្រី" key="ស្រី">ស្រី</Option>
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name=""
                        >
                            <Select placeholder="សញ្ជាតិ">
                                {nationalityData.map(load => {
                                    return <Option value={load.title} key={load.title}>{load.title}</Option>
                                })}
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="dob"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <DatePicker style={{width: "100%"}} placeholder="ថ្ងៃខែឆ្នាំកំណើត"/>
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="occupation"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="មុខរបរ" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 24, offset: 0 }}>
                        <Form.Item
                            name="province"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <ListSelect type={1} data={provinceData} title="ខេត្ត/ក្រុង" setValue={setToProviceFn} />
                        </Form.Item>
                    </Col>

                    {province === "សៀមរាប" ? (
                        <>
                            <Col xs={24} md={{ span: 11 }}>
                                <Form.Item
                                    name="district"
                                    rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                                >
                                    <ListSelect type={0} data={convertToDistrict(districtData)} title="ស្រុក/ខណ្ឌ" setValue={setToDistrictFn} disabled={province !== "សៀមរាប" ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="commune"
                                    rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                                >
                                    <ListSelect type={1} data={convertToCommune(district, communeData)} title="ឃុំ/សង្កាត់" setValue={setToCommuneFn} disabled={district === "" || district === null ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 24 }}>
                                <Form.Item
                                    name="village"
                                    rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                                >
                                    <ListSelect type={1} data={convertToVillage(commune, villageData)} title="ភូមិ" setValue={setToVillageFn} disabled={commune === "" || commune === null ? true : false} />
                                </Form.Item>
                            </Col>
                        </>
                    ) : null}

                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="social"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="បណ្តាញសង្គម" />
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="idCard"
                        // rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="អត្តសញ្ញាណប័ណ្ណ/លិខិតឆ្លងដែន" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="tel"
                            rules={[{ required: true, message: 'ត្រូវបំពេញប្រអប់ខាងលើ!' }]}
                        >
                            <Input placeholder="ទូរស័ព្ទ" addonBefore="+855" type="number" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Button 
                                type="primary"
                                style={{
                                    width:"100%"
                                }}
                                onClick={() => setOpenVaccine(true)}
                            >
                                ចាក់វ៉ាក់សាំង
                            </Button>
                    </Col>

                    <Col
                        xs={24}
                        md={{span: 24, offset: 0}}
                    >
                        <Button 
                                type="primary" 
                                htmlType="submit"
                                style={{
                                    width:"100%"
                                }}
                            >
                                បញ្ចូល
                            </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
